<template>
	<view>
		<view v-for="item in commentsList" :key="item.comments_id" class="comments-content">
			<comments-box :comments="item"></comments-box>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				commentsList: [],
				article_id: '',
				page: 1,
				pageSize: 5
			};
		},
		onLoad(query) {
			this.article_id = query.id
			// console.log(query);
			this.getComments()
		},
		methods: {
			getComments() {
				this.$api.get_comments({
					article_id: this.article_id,
					page: this.page,
					pageSize: this.pageSize,
				}).then(res => {
					const {data} = res
					console.log(res);
					this.commentsList = data
					// const {data} = res
					// this.commentsList = data
				})
			},
		},
	}
</script>

<style lang="scss">
	.comments-content {
		padding: 0 15px 1px;
		border-top: 1px solid #ccc;
	}
</style>
